@import "color";

nav {
  background: $backgroundColor;
  color: $bodyColor;
  margin:0; 
  padding:0;

  ul {
    list-style:none; 
    margin:0; 
    padding:0; 
    display:block;
    li {
      display:inline-block; 
      margin-bottom: 
      15px; margin-left: 
      15px;
    }
 
  }
  a {
    text-decoration:none; 
    padding:3px; 
    display: block; 
    margin-top:10px;
  } 
  a:link, a:visited {
    background-color: $backgroundColor; 
    color:$bodyColor;
  }
  a:hover, a:active {
    background-color: $backgroundColor2; 
    color:$bodyColor2;
  }
}

@media screen and (max-width: 450px) {
  nav ul li {
    display: block;
    margin: 0 auto;  
  } a {
    text-align: center;
  }
}